  color: #2f8e4a;
}

.header__search-section__result {
  background-color: #fff;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.16);
  visibility: hidden;
  opacity: 0;
  padding: 16px 24px 48px;
  border-radius: 8px;
  position: absolute;
  z-index: 4;
  top: 0;
  left: 25%;
  right: 25%;
  width: 50%;
  transform: translateY(16px);
  transition: all 350ms ease;
}
